
import React, { useState,useEffect } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import Aux from "../../../../hoc/_Aux";
import CustomInput from "../../../../Components/CustomInput";
import MessageModal from "./../../../../Components/Alert/Alert";
import i18n from './../../../../i18n';

import axios from 'axios';
var urlbackend = require("../../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, "City is Too Short!")
        .max(20, "City is Too Long!")
        .required("City is Required"),
    description: Yup.string()
        .min(2, "description is Too Short!")
        .max(20, "description is Too Long!")
        .required("description is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2,i18n.t('error-short-code'))
        .required(i18n.t('error-required-code')),
    article: Yup.string()
        .min(2,i18n.t('error-short-article'))
        .required(i18n.t('error-required-article')),
});
function UpdateArticle(props) {
    const [show, setShow] = useState(false);
    const [ArticlesUpdate] = useState(props.Article)
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [showMessageParams, setShowMessageParams] = useState({
        display:false,
        type:"success",
        message:null
    })
    const handlSubmit = (values) => {
        setShowMessageParams({
            display:false,
            type:"success",
            message:null
        });
        console.log(values);
        console.log(props.Article.id)
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(urlbackend + "/article/" + ArticlesUpdate.id, values, config)
            .then(response => {
                setShowMessageParams({
                    display:true,
                    type:"success",
                    message:i18n.t('success-update-article')
                });
                console.log(response.data)
                props.change()
                handleClose()
            })
            .catch(e => {
                setShowMessageParams({
                    display:true,
                    type:"danger",
                    message:i18n.t('error-update-article')
                });
                console.log(e)
            })
    };
    useEffect( () => {
        // console.log("show message value:",showMessage);
    }, [showMessageParams]);
    const showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">{i18n.t('update-article')}</Card.Title>
                            </Card.Header>
                            {showMessageParams.display?
                            <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                            :null}
                            <Card.Body>
                                <Row>
                                    <Col md={6}>

                                        <CustomInput
                                            name="article"
                                            label={i18n.t('article')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.article}
                                            errors={errors.article}
                                            touched={touched.article}
                                        />
                                           <CustomInput
                                            name="code"
                                            label={i18n.t('code')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.code}
                                            errors={errors.code}
                                            touched={touched.code}
                                        />

                                    </Col>
                                    <Col md={6}>








                                    </Col>
                                </Row>

                                <Button
                                    onClick={() => {
                                        handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };
    return (
        <>
            <Button variant="info"
                onClick={handleShow}> <span className="fa fa-pencil"></span></Button>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{i18n.t('update-article')} :   </Modal.Title>
                </Modal.Header>
                <Modal.Body>


                    <Formik
                        initialValues={{
                            article: `${ArticlesUpdate.article}`,
                            code: `${ArticlesUpdate.code}`,
                        }}
                        onSubmit={(values, { setSubmitting }) => {
                            this.submitForm(values);
                            setSubmitting(false);
                        }}
                        validationSchema={SignupSchema}
                    >
                        {(props) => showForm(props)}
                    </Formik>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                    {i18n.t('close')}
            </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default UpdateArticle