import React from 'react';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { Link } from "react-router-dom"
import axios from 'axios'
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import
import Aux from "../../../../hoc/_Aux";
import UpdateArticle from './Update.article'
import i18n from './../../../../i18n';

var urlbackend = require('../../../../env/env')();


class ListArticle extends React.Component {
    state = {
        Articles: [],
    };
  
    componentDidMount() {

        const config = {
            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Headers': 'Authorization',
              "x-access-token": localStorage.getItem('token'),
              'Authorization': 'Bearer '+localStorage.getItem('token'),
            },
          };
      
          axios.get(urlbackend + "/articles", config)
            .then(response => {
              console.log("this respon list", response.data)
              this.setState({ Articles: response.data.data })
      
            })
            .catch(e => {
              console.log(e)
            })
        
    }

    Delette(Article){
        console.log("hellodelet", Article.id)
        confirmAlert({
            title: 'Confirm Delete',
            message: 'Are you sure to do Delete?',
            buttons: [
              {
                label: 'Yes',
                onClick: () => {
                    axios.delete("http://localhost:5000/api/v1/Articles/"+Article.id).then(()=>{
            
                        var array = [...this.state.Articles]; 
                        var index = array.indexOf(Article)
                        if (index !== -1) {
                          array.splice(index, 1);
                          this.setState({Articles: array});
                        }
            
                   
                    })
                }
              },
              {
                label: 'No',
                onClick: null
              }
            ]
          });
      /*  */
    }
    render() {
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5"><Link to="/Add/Article" >  <Button>{i18n.t('add-article')}</Button> </Link> </Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>{i18n.t('code')}</th>
                                            <th>{i18n.t('article')}</th>
                                            <th>{i18n.t('action-event')}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.Articles.map((Article, index) => (

                                            <tr key={Article.id} >
                                               
                                                <td>{Article.code} </td>
                                                <td>{Article.article} </td>
                                                <td>  <Button variant="danger"
                                                    onClick={()=> this.Delette(Article)}> <span className="fa fa-trash"></span></Button> 
                                                    
                                                   
                                                    <UpdateArticle Article={Article} change={()=>this.componentDidMount()} />

                                                    </td>
                                            </tr>


                                        ))}
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default ListArticle;